.events-calendar-time-view{display:flex;flex-direction:column;height:100%}.events-calendar-time-view-scroll-wrapper{height:100%;margin-right:.125rem;overflow:auto}.events-calendar-time-view-grid-wrapper{display:flex}.events-calendar-time-view-grid{display:grid;flex-grow:1;grid-template-columns:repeat(1,minmax(0,1fr))}.events-calendar-time-view-grid[data-isweekview=true]{grid-template-columns:repeat(7,minmax(0,1fr))}