.events-calendar-show-more-text-container{border-radius:.25rem;color:var(--ec-gray-7);cursor:pointer;display:flex;font-size:.75rem;font-weight:600;line-height:normal;overflow:hidden;padding:2px 6px;position:relative;user-select:none;white-space:nowrap;z-index:1}.events-calendar-show-more-text-container[data-sm=true]{font-size:.65rem;margin-bottom:0;padding:1px 6px}.events-calendar-show-more-text-container:hover{background:var(--ec-gray-2);color:var(--ec-gray-8)}@media (max-width:640px){.events-calendar-show-more-text-container{outline:1px solid var(--ec-gray-3)}}.events-calendar-show-more-text-label{display:none;pointer-events:none}@media (max-width:640px){.events-calendar-sm-only{display:block}}@media (min-width:640px){.events-calendar-lg-only{display:block}}