.events-calendar-time-view-header{display:grid;font-size:.875rem;font-weight:600;grid-template-columns:repeat(1,minmax(0,1fr));margin-left:2.25rem;margin-right:.5rem;position:relative}.events-calendar-time-view-header[data-sm=true]{font-size:.75rem}.events-calendar-time-view-header[data-isweekview=true]{grid-template-columns:repeat(7,minmax(0,1fr))}.events-calendar-time-view-header-cell{text-align:center}.events-calendar-time-view-header-cell[data-border=true]{border-bottom:1px solid var(--ec-gray-4)}.events-calendar-time-view-header-label{background:transparent;padding:.25rem 0;user-select:none}.events-calendar-time-view-header-label[data-today=true]{background:#329aef;color:#fff}