.events-calendar-month-header{display:grid;font-size:.875rem;font-weight:600;grid-template-columns:repeat(7,minmax(0,1fr));position:relative}.events-calendar-month-header-cell{border-bottom:1px solid var(--ec-gray-4);color:var(--ec-gray-7);padding:.25rem 0;text-align:center;transition-duration:.75s;transition-property:background;transition-timing-function:cubic-bezier(.4,0,.2,1);user-select:none}