.events-calendar-overflow-card{background:var(--ec-default-background);border:1px solid var(--ec-gray-3);border-radius:.5rem;box-shadow:0 0 10px rgba(0,0,0,.5);box-sizing:border-box;display:flex;flex-direction:column;overflow:hidden;padding:.5rem .5rem .625rem;position:relative;width:13rem}.events-calendar-overflow-card *{box-sizing:border-box}.events-calendar-overflow-card-content{display:flex;flex-direction:column;gap:.125rem;max-height:min(308px,90vh);overflow-y:auto}.events-calendar-overflow-card-label{color:var(--ec-gray-9);font-size:.875rem;font-weight:600;margin-bottom:.5rem;text-align:center}.events-calendar-overflow-card-text{color:var(--ec-gray-7);font-size:.75rem;text-align:center}