.events-calendar-month-cell-header{align-items:center;display:flex;justify-content:space-between;user-select:none;width:100%}.events-calendar-month-cell-date-container{background:transparent;border-radius:9999px;box-sizing:border-box;display:flex;height:20px;justify-content:center;margin-left:1px;padding:.25rem;pointer-events:none;width:20px}.events-calendar-month-cell-date-container[data-today=true]{background:#329aef}.events-calendar-month-cell-date-label{font-size:.75rem;font-weight:500;line-height:1}.events-calendar-month-cell-date-label[data-color=white]{color:#fff}.events-calendar-month-cell-date-label[data-color=dark]{color:var(--ec-gray-7)}.events-calendar-month-cell-date-label[data-color=dim]{color:var(--ec-gray-5)}