.events-calendar-year-view-scroll-wrapper{height:100%;margin:.125rem;overflow:auto;padding:.75rem 1rem 2rem}.events-calendar-year-view{column-gap:2rem;display:grid;flex-wrap:wrap;grid-template-columns:repeat(auto-fit,minmax(200px,1fr));margin:0 auto;max-width:1280px;row-gap:1rem}.events-calendar-year-view-month{display:flex;flex-direction:column;gap:.25rem}.events-calendar-year-view-month-dates{display:grid;grid-template-columns:repeat(7,1fr);row-gap:.25rem}.events-calendar-year-view-month-label{display:grid;font-size:.875rem;font-weight:500;margin-top:.25rem;place-content:center;user-select:none;width:100%}.events-calendar-year-view-month-header{color:#868e96;display:grid;font-size:.75rem;font-weight:500;height:1.375rem;place-content:center;user-select:none;width:1.375rem}