.events-calendar-year-view-month-date{border-radius:9999px;color:var(--ec-gray-9);cursor:pointer;display:grid;font-size:.7rem;height:1.375rem;line-height:1.375rem;place-content:center;user-select:none;width:1.375rem}.events-calendar-year-view-month-date:hover{background-color:var(--ec-gray-1)}.events-calendar-year-view-month-date[data-dimmed=true]{color:var(--ec-gray-5)}.events-calendar-year-view-month-date[data-active=true]{background-color:#20c997;color:#fff}.events-calendar-year-view-month-date[data-active=true]:hover{background-color:#12b886}.events-calendar-year-view-month-date[data-today=true]{background-color:#228be6;color:#fff}