.events-calendar-month-cell{display:flex;flex-direction:column;padding:.125rem;transition-duration:.75s;transition-property:background;transition-timing-function:cubic-bezier(.4,0,.2,1);user-select:none}.events-calendar-month-cell[data-border=true]{border-right:1px solid var(--ec-gray-3)}.events-calendar-month-cell-content{display:flex;flex-direction:column;gap:.125rem;height:100%;justify-content:flex-end;overflow:hidden}.events-calendar-month-cell-content[data-week=true]{padding-bottom:.5rem}